import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home'
// 发现
const FindPage = resolve => require(['@/pages/findPage'], resolve)
const LearnPage = resolve => require(['@/pages/learnPage'], resolve)
const MinePage = resolve => require(['@/pages/minePage'], resolve)
const Details = resolve => require(['@/pages/details'], resolve)
const DetailsCenter = resolve => require(['@/pages/detailsCenter'], resolve)
const Collection = resolve => require(['@/pages/collection'], resolve)
const FollowPage = resolve => require(['@/pages/followPage'], resolve)
const Coupon = resolve => require(['@/pages/coupon'], resolve)
const CheckIn = resolve => require(['@/pages/checkIn'], resolve)



Vue.use(Router)

export default new Router({
  routes: [
    {
      path:"/details",
      name:"详情",
      component:Details
    },
    {
      path:"/detailsCenter",
      name:"详情中转",
      component:DetailsCenter
    },
    {
      path: '/',
      redirect:"/findPage",
      component: Home,
      children:[
        {
          // 发现
          path: '/findPage',
          name: '发现',
          component: FindPage
        },
        {
          path: '/learnPage',
          name: '学习',
          component: LearnPage
        },
        {
          path: '/minePage',
          name: '我的',
          component: MinePage
        }
      ]
    },
    {
      path:"/collection",
      name: '收藏列表',
      component: Collection
    },
    {
      path:"/followPage",
      name: '关注',
      component: FollowPage
    },
    {
      path:"/coupon",
      name: '卡券',
      component: Coupon
    },
    {
      path:"/checkIn",
      name: '入住',
      component: CheckIn
    },

  ]
})



