<template>
  <div id="homePage">
    <router-view class="tbp"></router-view>
    <Nav :nav="nav"></Nav>
  </div>
</template>

<script>
import Nav from "@/components/nav"
import { mapState, mapMutations, mapActions } from 'vuex'

export default {
  name: 'Home',
  data () {
    return {

    }
  },
  components:{
    Nav
  },
  computed:{
    ...mapState({
      nav:state => state.CommonStore.nav
    })
  },
  created(){

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .tbp{
    padding:0 0 0.64rem 0;
  }
</style>



